<template>
    <div class="home">
        <!-- <div class="head">
            <p class="head-log">ele.me</p>
            <div>登陆|注册</div>
        </div> -->
        <Head headbacks='true'></Head>
        <div class="news">
            <div class="news1">当前定位城市</div>
            <div class="news2">定位不准确，请在城市列表中选择</div>
        </div>
         <div class="new">
            <router-link tag='div' class="new1" :to="{name:'Scity',params:{id:wherelist.id,name:wherelist.name}}">{{wherelist.name}}</router-link>
            <div class="new2"><i class="el-icon-arrow-right"></i></div>
        </div>
        <div class="hotcity">
            <div class="hottitle">热门城市</div>
            <div class="hot">
                <ul>
                    <router-link v-for="(item,index) of hotlist" :key="index" :to="{name:'Scity',params:{name:item.name,id:item.id}}" tag="li">{{item.name}}</router-link>
                </ul>
            </div>
        </div>
        <div class="sortcity" v-for="(item,index) of qxcity" :key="index" >
            <div class="sorttitle" >{{item}}(按字母排序)</div>
            <div class="city">
                <ul>
                    <!-- <li v-for="(i,index) of qbcity[item]" :key="index">{{i.name}}</li> -->
                    <router-link v-for="(i,index) of qbcity[item]" :key="index" :to="{name:'Scity',params:{name:i.name,id:i.id}}" tag="li">{{i.name}}</router-link>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import Head from "./../header/Head.vue"
export default {
  components: {
    Head,
  },
  data() {
    return {
      wherelist: "",
      hotlist: [],
      qbcity: [],
      qxcity:[],
    };
  },
  methods: {
    getwlist() {
      var that = this;
      this.http.get("http://elm.cangdu.org/v1/cities?type=guess").then(res => {
        // console.log(res);
        that.wherelist = res;
      });
    },
    gethot() {
      var that = this;
      this.http.get("http://elm.cangdu.org/v1/cities?type=hot").then(res => {
        // console.log(res);
        that.hotlist = res;
      });
    },
    getqx() {
      var that = this;
      this.http.get("http://elm.cangdu.org/v1/cities?type=group").then(res => {
        // console.log(res);

        that.qbcity = res
        that.qxcity=Object.keys(res).sort()
        // console.log(that.qxcity);
      });
    }
  },
  mounted() {
    this.getwlist();
    this.gethot();
    this.getqx();
  }
};
</script>

<style lang="scss"  scoped>
.home {
  width: 7.5rem;
  height: 100%;
}
.news {
  width: 100%;
  height: 0.68rem;
  padding-top: 1rem;
  border-bottom: 0.01rem solid #ccc;
  display: flex;
  justify-content: space-between;
  line-height: 0.8rem;
  background: white;
  .news1 {
    font-size: 0.24rem;
    margin-left: 0.2rem;
  }
  .news2 {
    font-size: 0.2rem;
    font-weight: bold;
    color: #9f9f9f;
    margin-right: 0.2rem;
  }
}
.new {
  width: 100%;
  height: 0.68rem;
  border-bottom: 0.01rem solid #ccc;
  display: flex;
  justify-content: space-between;
  line-height: 0.8rem;
  background: white;
  .new1 {
    font-size: 0.3rem;
    color: #3190e8;
    margin-left: 0.2rem;
  }
  .new2 {
    font-size: 0.5rem;
    color: #ccc;
    margin-right: 0.2rem;
  }
}
.hotcity {
  width: 100%;
  height: 2.38rem;
  margin-top: 0.2rem;
  background: white;
  .hottitle {
    width: 90%;
    height: 0.72rem;
    padding-left: 0.2rem;
    text-align: left;
    font-size: 0.24rem;
    line-height: 0.72rem;
  }
  .hot {
    width: 100%;
    height: 1.66rem;
    ul {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      height: 100%;
      li {
        width: 1.855rem;
        height: 0.81rem;
        border: 0.01rem solid #ccc;
        font-size: 0.26rem;
        color: #3190e8;
        line-height: 0.81rem;
      }
    }
  }
}
.sortcity {
  width: 100%;
  height: auto;
  margin-top: 0.2rem;
  border-top: 0.01rem solid #ccc;
  background: white;
  .sorttitle {
    width: 90%;
    height: 0.72rem;
    line-height: 0.72rem;
    font-size: 0.24rem;
    text-align: left;
    margin-left: 0.2rem;
  }
  .city {
    width: 100%;
    height: auto;
    ul {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      height: 100%;
      li {
        width: 1.855rem;
        height: 0.81rem;
        border: 0.01rem solid #ccc;
        font-size: 0.24rem;
        line-height: 0.81rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
}
</style>



